/*
  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/
*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html, body {
  height: 100%;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

#root, #__next {
  isolation: isolate;
}

html {
  font-size: 62.5%;
}

body {
  overflow-x: hidden;
  box-sizing: border-box;
  font-family: "Noto Sans JP";
  font-style: normal;
  font-weight: 100;
}

a {
  text-decoration: none;
  display: inline-block;
}

img {
  display: inline-block;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  padding: 0;
}

button {
  padding: 0;
  background-color: transparent;
  border: none;
  appearance: none;
  cursor: pointer;
}

.c-font__list--header {
  font-family: coquette, sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 2.4rem;
  color: #157788;
}
.c-font__heading--about {
  font-family: coquette, sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 9vw;
  color: #157788;
}
.c-font__sub-heading--about {
  font-family: fot-tsukuardgothic-std, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1.6rem;
  color: #157788;
}
.c-font__heading--post {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 2.4rem;
  font-weight: 100;
}
.c-font__text {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.6rem;
  font-weight: 100;
  line-height: 2.8rem;
}

.c-icon__about {
  width: 120px;
  height: auto;
}

.c-img__background--header--sp {
  width: 100vw;
  height: auto;
}
.c-img__background--header--pc {
  width: 100vw;
  height: auto;
}
@media screen and (min-width: 1025px) {
  .c-img__background--header--pc {
    width: min(85vw, 1632px);
  }
}
.c-img__title--header--front {
  width: max(50vw, 190px);
  height: auto;
}
@media screen and (min-width: 600px) {
  .c-img__title--header--front {
    width: min(30vw, 576px);
  }
}
.c-img__main--header--sp {
  width: 100vw;
  height: auto;
}
.c-img__main--header--pc {
  width: 100vw;
  height: auto;
}
.c-img__catchcopy--pc {
  width: 32vw;
  height: auto;
}
.c-img__catchcopy--sp {
  width: 43vw;
  height: auto;
}
.c-img__present {
  width: 31vw;
  height: auto;
}
@media screen and (min-width: 1025px) {
  .c-img__present {
    width: 20vw;
    height: auto;
  }
}

.c-btn__nav--open {
  width: 10vw;
  height: auto;
}
@media screen and (min-width: 600px) {
  .c-btn__nav--open {
    width: 6vw;
  }
}

.c-circle--large {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background-color: #B1d3DD;
  box-shadow: inset 2px 2px 5px rgba(78, 86, 104, 0.55);
}
.c-circle--small {
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background-color: #B1d3DD;
  box-shadow: inset 2px 2px 5px rgba(78, 86, 104, 0.55);
}

.p-header__content {
  position: relative;
  z-index: 100;
}
.p-header__background--sp {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 101;
}
@media screen and (min-width: 600px) {
  .p-header__background--sp {
    display: none;
  }
}
.p-header__background--pc {
  display: none;
}
@media screen and (min-width: 600px) {
  .p-header__background--pc {
    position: absolute;
    z-index: 101;
    display: block;
    top: 0;
    right: 0;
  }
}
@media screen and (min-width: 1025px) {
  .p-header__background--pc {
    right: 0;
  }
}
.p-header__title {
  display: flex;
  justify-content: center;
  position: absolute;
  top: 4%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 102;
}
@media screen and (min-width: 600px) {
  .p-header__title {
    top: 2%;
  }
}
.p-header__main {
  position: relative;
}
@media screen and (min-width: 1025px) {
  .p-header__main {
    padding-top: 15vw;
  }
}
@media screen and (min-width: 1025px) {
  .p-header__img--sp {
    display: none;
  }
}
.p-header__img--pc {
  display: none;
}
@media screen and (min-width: 1025px) {
  .p-header__img--pc {
    display: block;
  }
}
.p-header__catchcopy--pc {
  display: none;
}
@media screen and (min-width: 1025px) {
  .p-header__catchcopy--pc {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 102;
    transform: translateX(-50%);
  }
}
.p-header__catchcopy--sp--lv1 {
  position: absolute;
  top: 35%;
  right: 28%;
  z-index: 102;
}
@media screen and (min-width: 1025px) {
  .p-header__catchcopy--sp--lv1 {
    display: none;
  }
}
.p-header__catchcopy--sp--lv2 {
  position: absolute;
  top: 56%;
  right: 15%;
  z-index: 102;
}
@media screen and (min-width: 1025px) {
  .p-header__catchcopy--sp--lv2 {
    display: none;
  }
}
.p-header__present {
  position: absolute;
  top: 51%;
  left: 7%;
  z-index: 99;
}
@media screen and (min-width: 1025px) {
  .p-header__present {
    top: 26%;
    left: 18%;
  }
}
.p-header__nav {
  display: none;
  position: absolute;
  top: 0;
  right: -100%;
  z-index: 103;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
}
.p-header__nav::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  background-color: #B1d3DD;
  width: 50%;
  height: 100%;
}
@media screen and (min-width: 1025px) {
  .p-header__nav {
    display: block;
    position: static;
    background-color: transparent;
  }
  .p-header__nav::before {
    display: none;
  }
}
.p-header__close--btn {
  position: absolute;
  top: 8%;
  right: 14%;
  z-index: 106;
}
@media screen and (min-width: 1025px) {
  .p-header__close--btn {
    display: none;
  }
}
.p-header__menu {
  position: absolute;
  top: 20%;
  left: 70%;
  z-index: 104;
  width: auto;
}
@media screen and (min-width: 1025px) {
  .p-header__menu {
    top: 3%;
    left: 86%;
    height: auto;
  }
}
.p-header__list {
  width: 80px;
  height: auto;
  text-align: right;
}
.p-header__item {
  padding: 5px 0;
  margin: 5px 0;
}
.p-header__open--btn {
  position: absolute;
  top: 6.5%;
  right: 10%;
  z-index: 104;
}
@media screen and (min-width: 600px) {
  .p-header__open--btn {
    top: 3.5%;
    right: 5%;
  }
}
@media screen and (min-width: 1025px) {
  .p-header__open--btn {
    display: none;
  }
}

.p-about__container {
  padding: 0 20px;
  margin-top: 100px;
}
.p-about__heading {
  display: flex;
  justify-content: center;
  align-items: center;
}
.p-about__title--font {
  position: relative;
  z-index: 101;
}
.p-about__title--circle--large {
  position: relative;
  z-index: 100;
  margin-left: -25px;
}
.p-about__title--circle--small {
  width: 65px;
  display: flex;
  justify-content: space-between;
  margin-left: 16px;
}
.p-about__sbu-heading {
  text-align: center;
}
.p-about__box {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}
@media screen and (min-width: 1025px) {
  .p-about__box {
    flex-direction: row;
  }
}