html {
  font-size : 62.5%;
}

body {
  overflow-x: hidden;
  box-sizing: border-box;
  font-family: 'Noto Sans JP';
  font-style: normal;
  font-weight: 100;
}

a {
  text-decoration: none;
  display        : inline-block;
}

img {
  display: inline-block;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  padding: 0;
}

button {
  padding: 0;
  background-color: transparent;
  border: none;
  appearance: none;
  cursor: pointer;
}